  background-color: rgba(255, 124, 213, 0.9) !important;
}

.custom-control-input:checked ~ .custom-control-label::before {
  background-color: rgba(255, 124, 213, 0.9) !important;
  border: 1px solid rgba(255, 100, 206, 0.5) !important;
}

.custom-range::-moz-range-thumb {
  background-color: rgba(247, 0, 168, 0.9) !important;
  border: 1px solid rgba(255, 255, 255, 0.9) !important;
}

.custom-range::-moz-range-track {
  background-color: rgba(255, 164, 226, 0.9) !important;
  border: 1px solid rgba(255, 100, 206, 0.5) !important;
 
}



